<template>
  <v-container
    id="data-tables"
    class="fill-height align-start"
    fluid
    tag="section"
  >
    <v-container
      fluid
    >
      <ik-data-table
        ref="billTable"
        :use-default-action="false"
        :model="model"
        :filter="filter"
        :entity-name="$t('全部账单')"
        :icon="'mdi-format-list-bulleted'"
      >
        <template v-slot:filterLeft>
          <v-menu
            ref="menu"
            v-model="menu"
            :close-on-content-click="false"
            :return-value.sync="dates"
            offset-y
          >
            <template v-slot:activator="{ on }">
              <v-text-field
                v-model="dates"
                class="ml-auto"
                hide-details
                :label="$t('日期筛选')"
                prepend-icon="mdi-calendar"
                readonly
                style="max-width: 250px;"
                single-line
                append-icon="mdi-close"
                @click:append="clear"
                v-on="on"
              />
            </template>
            <v-date-picker
              v-model="dates"
              no-title
              range
              scrollable
              locale="de"
            >
              <v-spacer/>
              <v-btn
                text
                color="primary"
                @click="menu = false"
              >
                {{ $t('Cancel') }}
              </v-btn>
              <v-btn
                text
                color="primary"
                @click="$refs.menu.save(dates)"
              >
                {{ $t('OK') }}
              </v-btn>
            </v-date-picker>
          </v-menu>
        </template>
        <template
          v-if="$vuetify.breakpoint.mdAndUp"
          v-slot:filterRight="{ tableItems }"
        >
          <v-btn
            small
            color="green"
          >
            {{ $t('sumAmount') }}：€{{ calculateTotalPaymentPrice(tableItems).toFixed(2) }}
          </v-btn>
          <v-btn
            small
            color="#367aeb"
          >
            {{ $t('totalPrice') }}：€{{ calculateTotalPrice(tableItems).toFixed(2) }}
          </v-btn>
        </template>
        <template v-slot:footer="{selectItems}">
          <v-toolbar
            color="white"
            flat
          >
            <template v-for="(n, i) in billSelectionList">
              <v-btn
                :key="i"
                class="mr-1"
                :color="billSelectionColorList[i]"
                dark
                outlined
                @click="setBillDates(i)"
              >
                {{ n }}
              </v-btn>
            </template>
            <v-spacer/>
            <template v-if="selectItems.length>0">
              <v-btn
                class="mb-2"
                color="success"
                @click="reprint(selectItems)"
              >
                {{ $t('打印订单') }}
              </v-btn>
            </template>
            <v-btn
              class="mb-2"
              color="primary"
              dark
              @click="print()"
            >
              {{ $t('打印') }}
            </v-btn>
          </v-toolbar>
        </template>
        <template v-slot:item.action="{ item }">
          <v-icon
            class="mr-2"
            small
            @click="goNext(item)"
          >
            mdi-arrow-right
          </v-icon>
        </template>
      </ik-data-table>
      <v-dialog
        v-model="showDetail"
      >
        <v-card>
          <v-toolbar @click="showDetail=!showDetail">
            <v-spacer/>
            <v-btn icon>
              <v-icon>mdi-close</v-icon>
            </v-btn>
          </v-toolbar>
          <bill-detail-page :id="currentOrderId"/>
        </v-card>
      </v-dialog>
    </v-container>
  </v-container>
</template>

<script>

  import { printSummaryBon, reprintOrder } from '@/components/Utlis/api'
  import Utils from '../../components/Utlis/Utils'
  import HistoryBills from '../../model/HistoryBills'
  import { IkDataTable } from 'metaflow-js'
  import moment from 'moment'
  import BillDetailPage from '../Details/BillDetailPage'
  import { formatDate } from '@/common/common'

  export default {
    components: {
      BillDetailPage,
      IkDataTable,
    },
    data: function () {
      return ({
        model: HistoryBills,
        dates: [],
        timer: null,
        showDetail: null,
        currentOrderId: null,
        menu: false,
        modal: false,
        menu2: false,
        billSelectionList: [
          this.$i18n.t('当日结单'),
          this.$i18n.t('本周结单'),
          this.$i18n.t('本月结单'),
          this.$i18n.t('本年结单'),
        ],
        billSelectionColorList: ['green darken-2', 'blue darken-2', 'purple darken-2', 'pink'],
      })
    },
    computed: {
      filter () {
        if (Date.parse(this.dates[0]) > Date.parse(this.dates[1])) {
          [this.dates[0], this.dates[1]] = [this.dates[1], this.dates[0]]
        }
        return { timespan: this.dates ? this.dates.join(' - ') : '' }
      },
    },

    watch: {},
    async created () {
      this.dates = [formatDate(0), formatDate(0)]
    },
    methods: {
      calculateTotalPrice (items) {
        return items ? items.reduce(
          (arr, item) =>
            item.consumeTypeStatusId !== 5 ? arr + parseFloat(item.totalPrice) : arr
          , 0) : 0
      },
      calculateTotalPaymentPrice (items) {
        if (!this.$refs.billTable || !this.$refs.billTable.filterItem.paymentArr || (this.$refs.billTable.filterItem.paymentArr.length === 0)) {
          return items ? items.reduce(
            (arr, item) =>
              item.consumeTypeStatusId !== 5 ? arr + parseFloat(item.totalPrice) : arr
            , 0) : 0
        } else {
          const paymentArr = this.$refs.billTable.filterItem.paymentArr
          let totalMoney = 0
          paymentArr.forEach(indexItem => {
            for (const item of items) {
              if (item.payLogs !== null) {
                item.payLogs.forEach(payItem => {
                  if (parseInt(payItem.paymentMethodId) === parseInt(indexItem)) {
                    totalMoney += parseFloat(payItem.amount)
                  }
                })
              }
            }
          })
          return totalMoney
        }
      },
      goNext (item) {
        localStorage.setItem('selectData', JSON.stringify(this.dates))
        this.showDetail = true
        this.currentOrderId = item.orderId
      },
      clear () {
        this.dates = []
        localStorage.setItem('selectData', JSON.stringify(this.dates))
      },
      setBillDates (i) {
        switch (i) {
          case 0:
            this.dates = [formatDate(0), formatDate(0)]
            break
          case 1:
            this.dates = [moment().startOf('week').format('YYYY-MM-DD'), formatDate(0)]
            break
          case 2:
            this.dates = [moment().startOf('month').format('YYYY-MM-DD'), formatDate(0)]
            break
          case 3:
            this.dates = [moment().startOf('year').format('YYYY-MM-DD'), formatDate(0)]
        }
      },
      reprint (items) {
        Utils.showConfirm('Bist du sicher?', 'Möchten Sie alle Datensätze drucken?', () => {
          items.forEach(i => {
            reprintOrder(i.id, 0, 1)
          })
        })
      },
      print () {
        Utils.showConfirm('Bist du sicher?', 'Möchten Sie alle Datensätze drucken?', () => {
          printSummaryBon(this.filter).then(() => {
            Utils.toast('Erfolgreich drucken!')
          })
        })
      },
    },
  }
</script>
