// import GlobalSettings from '@/GlobalSettings'
import { IKDataEntity } from 'metaflow-js'
import hillo from 'hillo'

const entity = {
  id: {
    type: IKDataEntity.Types.Integer,
    displayName: 'id',
    tableConfig: {
      align: 'start',
    },
    formConfig: {
      requiredNew: false,
    },
  },
  name: {
    displayName: 'staffName',
  },
  printName: {
    displayName: 'printTitle',
  },
  zbonGroup: {
    displayName: 'zbonGroup',
  },
  color: {
    type: IKDataEntity.Types.Color,
    formConfig: {
      required: false,
    },
  },
}
const config = {
  load: async function (filter) {
    return (await hillo.get('Complex.php', {
      op: 'showAllConsumeTypeInfo',
      // lang: GlobalSettings.getLang(),
      ...filter,
    }))
      .content
  },
  add: function (item) {
    return hillo.post('ConsumeType.php?op=add', {
      ...item,
      langs: JSON.stringify(item.langs),
    }, {
      showLoading: true,
    })
  },
  edit: function (item) {
    return hillo.postWithUploadFile('ConsumeType.php?op=update', {
      ...item,
      langs: JSON.stringify(item.langs),
    }, {
      showLoading: true,
    })
  },
  remove: function (id) {
    return hillo.post('ConsumeType.php?op=delete', { id: id })
  },
}

export default IKDataEntity.ModelFactory(entity, config)
