import GlobalSettings from '@/GlobalSettings'

import { IKDataEntity } from 'metaflow-js'
import hillo from 'hillo'
import ConsumeType from './ConsumeType'
import Payment from './Payment'
import ConsumeTypeStatus from './ConsumeTypeStatus'
import PaymentColors from './PaymentColors'
import Servant from '@/model/Servant'
const entity = {
  orderId: {
    type: IKDataEntity.Types.Integer,
    displayName: 'id',
    tableConfig: {
      align: 'start',
    },
    formConfig: {
      disableNew: true,
      disableEdit: true,
      requiredNew: false,
    },
  },
  tableName: {},
  servantName: {
    type: IKDataEntity.Types.Option,
    formConfig: {
      type: {
        selectItems: Servant.getList,
        itemText: 'name',
        itemValue: 'name',
      },
    },
    displayName: 'servantName',
  },
  updateTimestamp: {
    displayName: '结账时间',
  },
  totalPrice: {},
  payMethod: {
    type: IKDataEntity.Types.Option,
    formConfig: {
      type: {
        selectItems: Payment.getList,
        itemText: '_langsname',
        color: PaymentColors.getList(),
      },
    },
    displayName: '付款状态',
  },
  tipIncome: {},
  consumeTypeId: {
    type: IKDataEntity.Types.Option,
    formConfig: {
      type: {
        selectItems: ConsumeType.getList,
      },
    },
  },
  consumeTypeStatusId: {
    displayName: 'consumeTypeStatus',
    type: IKDataEntity.Types.Option,
    formConfig: {
      type: {
        selectItems: ConsumeTypeStatus.getList,
        itemText: 'name',
      },
      merge: false,
    },
  },
  paymentStatus: {
    displayName: 'paymentStatus',
    type: IKDataEntity.Types.Boolean,
    formConfig: {
      merge: false,
    },
  },
  paymentStr: {
    formConfig: {
      required: false,
    },
    displayName: 'payMethod',
  },
  paymentArr: {
    displayName: 'Payment',
    type: IKDataEntity.Types.Option,
    header: false,
    formConfig: {
      required: false,
      type: {
        selectItems: Payment.getList,
        itemText: '_langsname',
        multiple: true,
        disabledItem: (item) => {
          if (item.id === 0) {
            return ({
              ...item,
              disabled: item,
            })
          } else {
            return item
          }
        },
      },
    },
  },
}
const config = {
  load: async function (filter) {
    return (await hillo.get('Orders.php', Object.assign({
      op: 'withSortAndFilter',
      lang: GlobalSettings.getLang(),
    }, ...filter)))
      .content.map(function (item) {
        if (item.payLogs) {
          item.paymentStr = []
          item.paymentArr = []
          item.payLogs.forEach(n => {
            if (n.paymentMethodId !== '9') {
              const paymentStr = n.name + ':' + n.amount
              item.paymentStr.push(paymentStr)
              item.paymentArr.push(n.paymentMethodId)
            }
          })
          item.paymentStr = item.paymentStr.join(' / ')
          item.paymentArr = String(item.paymentArr)
        }
        item.id = item.orderId
        return item
      })
  },
}
export default IKDataEntity.ModelFactory(entity, config)
